import React, { useState, useEffect, setState, classNames, state, toggleDropdown, isOpen, responsive } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink
} from "react-router-dom";
import "./Nav.css";

function Nav() {
  const [isActive, setIsActive] = useState("false");

  const logoPath = "/images/logo.png";

  const handleLink = () => {
    setIsActive(false);
  };

  const handleIcon = () => {
    setIsActive(isActive ? false : true);
  };

  return (
    <div className={`nav${isActive ? " responsive" : ""}`} id="myNav">
      <Link to="/" className="sticky nav-logo-link" exact={true} onClick={handleLink}><img className="nav-logo" src={logoPath} alt="Logo" /></Link>
      <Link to="/" className="sticky nav-item" exact={true} onClick={handleLink}>Dice Droppers</Link>
      <NavLink to="/" className="sticky nav-item home" exact={true} onClick={handleLink}>Home</NavLink>
      <NavLink to="/blog" className="nav-item" onClick={handleLink}>Blog</NavLink>
      <NavLink to="/episodes" className="nav-item" onClick={handleLink}>Episodes</NavLink>
      <NavLink to="/campaign-notes" className="nav-item" onClick={handleLink}>Campaign Notes</NavLink>
      <NavLink to="/meet-the-crew" className="nav-item" onClick={handleLink}>Meet The Crew</NavLink>
      <NavLink to="/search" className="nav-item" onClick={handleLink}>Search</NavLink>
      <a href="javascript:void(0);" className="nav-item icon" onClick={handleIcon}>
        <i className="fa fa-bars" />
      </a>
    </div>
  );
}

export default Nav;